import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
	const { data: ctbUtils } = useWPOptionQuery('ctbUtils');
	const { data = null, refetch, isLoading = true } = useWPAjax('ctbPipeChecker', { _wpnonce: ctbUtils?.nonce }, true);
	const isPremium = (!isLoading && data?.isPipe) || false;

	useEffect(() => {
		refetch()
	}, [ctbUtils]);

	return { isPremium, isLoading };
};
export default usePremiumInEditor;